body {
  margin: 0;
  padding: 0;
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #D0D6C9;
}

.chatbody{
  background-color: #009688;
  margin: 0 auto;
  position: absolute; 
  width: 100%;
  height: 7em;
}

.WhatsappChat{
  background-color: rgb(196, 201, 192);
  background-image: url("img\\backgroundwhatsapp.png");
  margin: 0 auto;
  position: fixed; 
  top: 10%;
  left: 30%;
  right: 30%;
  max-height: 200;
  overflow: auto;
  height: 81.3%;

  -webkit-box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);

          box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
}

.swapContainer {
  background-color: rgb(244, 245, 244);
  margin: 0 auto;
  position: fixed;
  top: 3%;
  left: 30%;
  right: 30%;
  max-height: 200;
  overflow: auto;
  height: 7%;
  text-align: center;
}
.dropzone{
  height: 7em;
  border-radius: 10%;
  position: fixed;
  right: 15%;
  width: 14%;
}

.dropzone div {
  border-radius: 7.5px;
  -webkit-box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
          box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
}

.Card{
  max-width: 400px;
}

.message {
  border-radius: 7.5px;
  -webkit-box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
          box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
}


.chatBar{
  background-color: #eeefee;
  margin: 0 auto;
  position: fixed; 
  top: 3%;
  left: 20%;
  right: 70%;
  max-height: 200;
  overflow: auto;
  height: 90%;

  -webkit-box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);

          box-shadow: 0 1px 0.5px rgba(0, 0, 0, 0.13);
}

.SearchBar{
  height: 7em;
  border-radius: 10%;
  position: fixed;
  right: 15%;
  width: 14%;
  top:20%
}

.date_container{
  position: relative;
  margin: 10px;
  clear: both;
  overflow: hidden;
  text-align: center;
}

.date {
  background-color: rgba(225, 245, 254, 0.92);
  border-radius: 7.5px;
  text-align: center;
  margin-left: 42%!important;
  margin-right: 42%!important;
}

.days_container {
  margin: 5px;
}

.day{
  margin: 5px;
}

.currentDays{
  position: fixed;
  left: 2%;
  top: 2%;
}

/* SCROLLBAR */

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.mobile_upload{
  display: none!important;
}


@media only screen and (max-width: 600px) {
  .WhatsappChat {
    left: 0;
    right: 0;
    bottom: 20%;
    top: 8%;
    height: auto;
  }
  .date{
    margin-left: 30%!important;
    margin-right: 30%!important;
  }

  .chatBar {
    position: fixed;
    left: 0;
    right: 0;
    top: 85%;
  }

  .days_container{
    position: fixed;
    top: 85%;
    width: 100%;
    left: 0;
    right: 0;
    height: auto;
    bottom: 0%;
    overflow: auto;
  }

  .dropzone{
    width: 30%;
    right: 10%;
    text-align: center;
    top: 4%;
  }

  .dropzone div div h3 {
    font-size: 1rem;
  }

  .dropzone div div {
    padding: 6%;
    padding-bottom: 12px!important;
  }

  .swapContainer{
    left: 0;
    right: 0;
  }
  .swapButton {
    float: left;
    left: 10%;
    margin-top: 6px!important;
  }
  .SearchBar{
    left: 2%;
    right: 0;
    top: 77%;
    width: 96%;
  }
  .searchInfo{
    display: none!important;
  }
  .desktop_upload{
    display: none!important;
  }
  .mobile_upload{
    display: block!important;
  }
}


